import { SyncData } from './SyncData';
import { ConfigManager } from '../ConfigManager';
import { useState, useEffect } from 'preact/hooks';
import { h } from 'preact';
import { WebDavSignal } from '../WebDavSignal';
import { RtcClient } from '../RtcClient';
import { RtcState } from '../RtcBase';
import { DirSync } from '../DirSync';

const configManager = new ConfigManager();
let dirSync = new DirSync();

export function Client({ id, iceServers }) {
  const signal = new WebDavSignal(configManager.webDavCreds);
  const [state, setState] = useState(RtcState.INACTIVE);
  const [input, setInput] = useState('');
  const [client, setClient] = useState(null);
  useEffect(() => {
    const client = new RtcClient({ iceServers }, signal);
    client.addEventListener('message', ({ type, data }) => {
      if (type === 'json') {
        if (data.type === 'text') {
          setInput(data.value);
        } else if (data.type === 'file') {
          dirSync.writeFileMetadata(data.value);
        }
      } else {
        dirSync.writeFile(data);
      }
    });
    client.addEventListener('state', (event) => {
      setState(event.state);
    });
    client.connect(id);
    setClient(client);
    return () => {
      client.close();
    };
  }, [id]);
  const onChange = (value) => {
    setInput(value);
    client.sendMessage({
      type: 'text',
      value,
    });
  };
  const onFolderPick = async () => {
    const file = await dirSync.getFile();
    await dirSync.sendFile(file, (data) => client.sendMessage(data));
  };
  return (
    <SyncData
      value={input}
      onChange={onChange}
      onFolderPick={onFolderPick}
      disabled={state !== RtcState.CONNECTED}
    />
  );
}
