import * as crc32 from 'crc-32';

export class DirSync {
  #files = new Map();

  async getFile() {
    return new Promise((resolve, reject) => {
      const input = document.createElement('input');
      input.type = 'file';
      input.onchange = (event) => {
        const { files } = event.target;
        if (files.length === 1) {
          resolve(files[0]);
        } else {
          reject(new Error('No file selected'));
        }
      };
      input.click();
    });
  }

  async sendFile(file, sender) {
    let fileOffset = 0;
    let sizeRemaining = file.size;
    let chunkIndex = 0;

    sender({
      type: 'file',
      value: {
        name: file.name,
        size: file.size,
        type: file.type
      },
    });
    const buffer = await this.#readFile(file);

    const id = crc32.str(file.name);
    while (sizeRemaining > 0) {
      const headerBuffer = new ArrayBuffer(8);
      const headerView = new DataView(headerBuffer);
      headerView.setInt32(0, id, true);
      headerView.setUint32(4, chunkIndex, true);
      let chunkSize = 8192;
      if (sizeRemaining < chunkSize) {
        chunkSize = sizeRemaining;
      }
      const chunk = buffer.slice(fileOffset, chunkSize + fileOffset);
      fileOffset += chunkSize;
      sizeRemaining -= chunkSize;
      chunkIndex++;

      const blob = new Blob([headerBuffer, chunk]);

      sender(await blob.arrayBuffer());
    }
  }

  async #readFile(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.addEventListener('load', () => {
        resolve(reader.result);
      });
      reader.addEventListener('error', () => {
        reject();
      });
      reader.readAsArrayBuffer(file);
    });
  }

  async writeFileMetadata(metadata) {
    const id = crc32.str(metadata.name);
    let file = this.#files.get(id);
    if (!file) {
      file = {
        written: 0,
        chunks: []
      };
      this.#files.set(id, file);
    }
    file.name = metadata.name;
    file.size = metadata.size;
    file.type = metadata.type;
    this.#verifyFile(id);
  }

  async writeFile(buffer) {
    const headerBuffer = buffer.slice(0, 8);
    const headerView = new DataView(headerBuffer);
    const id = headerView.getInt32(0, true);
    const chunkIndex = headerView.getUint32(4, true);
    const chunk = buffer.slice(8);
    let file = this.#files.get(id);
    if (!file) {
      file = {
        written: 0,
        chunks: []
      };
      this.#files.set(id, file);
    }
    file.chunks[chunkIndex] = chunk;
    file.written += chunk.byteLength;
    this.#verifyFile(id);
  }

  #verifyFile(id) {
    const file = this.#files.get(id);
    if (file && file.written === file.size) {
      const blob = new Blob(file.chunks);
      this.#downloadBlob(file.name, blob);
      this.#files.delete(id);
    }
  }

  #downloadBlob(name, blob) {
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = name;
    a.click();
    URL.revokeObjectURL(url);
  }
}
